<template>
  <div class="cart">
    <el-breadcrumb separator="/" v-if="!isComponents">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>购物车</el-breadcrumb-item>
    </el-breadcrumb>
    <el-table :data="lists">
      <el-table-column width="60px" v-if="!isComponents">
        <template slot="header" slot-scope="scope">
          <el-checkbox v-model="handleCheckedAllStatus"></el-checkbox>
        </template>
        <template slot-scope="scope">
          <el-checkbox
            :value="scope.row.checked"
            @change="
              (val) =>
                SET_CART({ type: 'update', id: scope.row.id, checked: true })
            "
          ></el-checkbox>
        </template>
      </el-table-column>
      <el-table-column prop="title" label="商品"></el-table-column>
      <el-table-column prop="price" label="单价"></el-table-column>
      <el-table-column label="数量">
        <template slot-scope="scope">
          <el-input-number
            v-if="!isComponents"
            :value="scope.row.count"
            :min="1"
            size="mini"
            @change="(cur, old) => handleChange(cur, old, scope.row.id)"
          ></el-input-number>
          <span v-else>{{ scope.row.count }}</span>
        </template>
      </el-table-column>
      <el-table-column label="小计">
        <template slot-scope="scope">
          {{ (scope.row.price * scope.row.count).toFixed(2) }}
        </template>
      </el-table-column>
      <el-table-column>
        <template slot-scope="scope">
          <el-button
            size="small"
            @click="SET_CART({ type: 'del', id: scope.row.id })"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <div style="padding: 15px 0" v-if="!isComponents">
      <p>已选 {{ checkedCount }} 件商品，总价：{{ checkedPrice }}</p>
    </div>
    <div style="padding: 15px 0" v-else>
      <p>共计 {{ totalCount }} 件商品，总价：{{ totalPrice }}</p>
    </div>
    <el-button type="danger" v-if="!isComponents">结算</el-button>
    <el-button type="danger" @click="$router.push('/cart')" v-else
      >去购物车</el-button
    >
  </div>
</template>

<script>
import { mapState, mapMutations, mapGetters } from "vuex";
export default {
  name: "",
  data() {
    return {};
  },
  props: {
    isComponents: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    ...mapState({
      lists: "cartLists",
    }),
    ...mapGetters([
      "checkedCount",
      "checkedPrice",
      "totalCount",
      "totalPrice",
      "checkedAllStatus",
    ]),
    handleCheckedAllStatus: {
      get() {
        return this.checkedAllStatus;
      },
      set(val) {
        this.SET_CART({ type: "update", all: true, val });
      },
    },
  },
  created() {},
  methods: {
    ...mapMutations(["SET_CART"]),
    //监控加减事件
    handleChange(cur, old, id) {
      console.log(cur, old, id);
      if (old - cur === 1) {
        this.SET_CART({ type: "sub", id });
      } else if (cur - old === 1) {
        this.SET_CART({ type: "add", id });
      } else {
        this.SET_CART({ type: "update", id, count: cur });
      }
    },
  },
};
</script>

<style scoped lang="scss"></style>
