import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    //商品列表
    goodsLists: [],
    //购物车列表
    cartLists: localStorage.getItem("cartLists")
      ? JSON.parse(localStorage.getItem("cartLists"))
      : [],
  },
  getters: {
    cartTotal(state) {
      let total = 0;
      state.cartLists.map((item) => {
        total += item.count;
      });
      return total;
    },
    checkedCount(state) {
      let total = 0;
      state.cartLists.map((item) => {
        if (item.checked) {
          total += item.count;
        }
      });
      return total;
    },
    checkedPrice(state) {
      let total = 0;
      state.cartLists.map((item) => {
        if (item.checked) {
          total += item.count * item.price;
        }
      });
      return total.toFixed(2);
    },
    totalCount(state) {
      let total = 0;
      state.cartLists.map((item) => {
        total += item.count;
      });
      return total;
    },
    totalPrice(state) {
      let total = 0;
      state.cartLists.map((item) => {
        total += item.count * item.price;
      });
      return total.toFixed(2);
    },
    checkedAllStatus(state) {
      console.log(123);
      return state.cartLists.every((item) => item.checked);
    },
  },
  mutations: {
    //设置商品列表数据
    SET_GOODS(state, lists) {
      state.goodsLists = lists;
    },
    //添加购物车商品payload:{type:'add','sub','del','update',id: '',count: ''}
    SET_CART(state, payload) {
      let index = state.cartLists.findIndex((item) => item.id === payload.id);
      switch (payload.type) {
        case "add":
          let goods = state.goodsLists.find((item) => item.id === payload.id);
          if (index >= 0) {
            state.cartLists[index].count += 1;
          } else {
            state.cartLists.push({
              id: payload.id,
              title: goods.title,
              price: goods.price,
              count: 1,
              checked: false,
            });
          }
          localStorage.setItem("cartLists", JSON.stringify(state.cartLists));
          break;
        case "sub":
          state.cartLists[index].count -= 1;
          localStorage.setItem("cartLists", JSON.stringify(state.cartLists));
          break;
        case "update":
          if (payload.count) {
            state.cartLists[index].count = payload.count;
          }
          if (payload.checked) {
            state.cartLists[index].checked = !state.cartLists[index].checked;
          }
          if (payload.all) {
            state.cartLists.forEach((item) => (item.checked = payload.val));
          }
          localStorage.setItem("cartLists", JSON.stringify(state.cartLists));
          break;
        case "del":
          state.cartLists.splice(index, 1);
          localStorage.setItem("cartLists", JSON.stringify(state.cartLists));
          break;
        default:
          break;
      }
    },
  },
  actions: {
    async get_goods({ commit }) {
      try {
        const res = await axios.get("http://127.0.0.1:3000/products");
        commit("SET_GOODS", res.data);
      } catch (error) {
        console.log(error);
      }
    },
  },
});
export default store;
