<template>
  <div class="home">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-table :data="lists">
      <el-table-column prop="title" label="商品"></el-table-column>
      <el-table-column prop="price" label="价格"></el-table-column>
      <el-table-column>
        <template slot-scope="scope">
          <el-button @click="handleAdd({ type: 'add', id: scope.row.id })"
            >加入购物车</el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapActions, mapState, mapMutations } from "vuex";
export default {
  name: "HomeView",
  data() {
    return {};
  },
  components: {},
  computed: {
    ...mapState({
      //商品列表
      lists: "goodsLists",
    }),
  },
  methods: {
    ...mapActions(["get_goods"]),
    ...mapMutations({
      handleAdd: "SET_CART",
    }),
  },
  created() {
    this.get_goods();
  },
};
</script>
<style lang="scss" scoped>
.home {
}
</style>
